<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级简介</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/editIntro'})">编辑</span>
        </div>
        <div class="c333 f28" style="padding:0.84rem 0 1rem;">
            <div class="nameBox">
                <img :src="data.classIamge" alt="">
            </div>
            <div class="content b_line">
                <p class="subtitle">班级简介</p>
                <p class="c666">{{data.classDesc}}</p>
            </div>
            <div class="content">
                <p class="subtitle">图片</p>
                <div class="picture">
                    <img :src="item" alt="" v-for="(item,index) in data.imageUrl" :key="index" @click="show(item)">
                </div>
                <p class="subtitle">视频</p>
                <div class="picture">
                    <video :src="item" alt="" v-for="(item,index) in data.videoUrls" :key="index" @click="play"></video>
                </div>
            </div>  
        </div>
    </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            data:{}
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/class'});//返回上一层
        },
        getClassInfo(){//获取班级信息
            this.$http({url:this.$api.class.classIntro},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    data.data.classIamge = this.$api.fileUrl + data.data.classIamge;
                    for(var i in data.data.imageUrl){
                        data.data.imageUrl[i] = this.$api.fileUrl + data.data.imageUrl[i]
                    }
                    for(var i in data.data.videoUrls){
                        data.data.videoUrls[i] = this.$api.fileUrl + data.data.videoUrls[i]
                    }
                    this.data = data.data;
                }else{
                    this.$toast(data.msg);
                }
            })
        },
        play(e){
            console.log(e)
            if(e.target.paused){
                e.target.play();
                
            }else{
                e.target.pause()
            }
        },
        show(url){
            ImagePreview([url]);
        }
    },
    created() {
        this.getClassInfo();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:.3rem;
    }
    .nameBox img{
        width:100%;
        box-shadow: 0 0 0.3rem #ccc;
        height: 3.88rem;
    }
    .name{
        border:0;
        text-align: right;
        width:50%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:2.67rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-right: 0.1rem
    }
    .picture video{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-right: 0.1rem
    }
</style>




